.skills-section {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.skills-heading {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 0.5rem;
  text-align: center;
}

.skill-item {
  display: flex;
  flex-direction: column;
  background: var(--card-bg);
  border: 1px solid var(--card-border);
  border-radius: 8px;
  overflow: hidden;
  transition: all 0.3s ease;
}

.skill-item:hover {
  border-color: var(--accent-purple);
}

.skill-button {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  background: transparent;
  border: none;
  color: var(--text-primary);
  cursor: pointer;
  text-align: left;
  width: 100%;
  transition: background 0.3s ease;
}

.skill-button:hover {
  background: rgba(139, 92, 246, 0.1);
}

.skill-icon {
  font-size: 1.5rem;
  color: var(--accent-purple-light);
  display: flex;
  align-items: center;
}

.skill-label {
  flex: 1;
  font-size: 0.95rem;
  font-weight: 500;
  letter-spacing: 0.05em;
}

.skill-chevron {
  font-size: 0.875rem;
  color: var(--text-secondary);
  transition: transform 0.3s ease;
}

.skill-chevron.expanded {
  transform: rotate(180deg);
}

.skill-content {
  padding: 0 1rem 1rem 1rem;
  color: var(--text-secondary);
  font-size: 0.9rem;
  line-height: 1.6;
  animation: slideDown 0.3s ease;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
