.profile-section {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 1rem;
}

.profile-picture {
  width: 200px;
  height: 200px;
  border-radius: 8px;
  object-fit: cover;
  border: 2px solid var(--accent-purple);
  margin-bottom: 0.5rem;
}

.profile-name {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--text-primary);
  letter-spacing: 0.05em;
  line-height: 1.2;
  margin: 0;
}

.profile-subtitle {
  font-size: 1rem;
  color: var(--text-secondary);
  font-weight: 400;
  margin: 0;
}

.profile-school {
  font-size: 0.95rem;
  color: var(--text-secondary);
  font-weight: 400;
  margin: 0;
}

@media (max-width: 768px) {
  .profile-name {
    font-size: 2rem;
  }
  
  .profile-picture {
    width: 150px;
    height: 150px;
  }
}
